<!--用于存放文章列表-->
<template>
  <div class="atc-item">
    <!--点击 路由到文章详情，动态传参文章id-->
    <van-cell @click="$router.push(`/article/${atcData.art_id}`)">
      <p slot="title" class="title van-multi-ellipsis--l3">{{atcData.title}}</p>
      <div slot="label">
        <div v-if="atcData.cover.type >= 2" class="imgData">
          <van-image
            class="img"
            v-for="(img, index) in atcData.cover.images"
            fit="cover"
            :key="index"
            :src="img"
          />
        </div>
        <div class="botTxt">
          <span v-if="atcData.is_top">置顶</span>
          <span>{{atcData.aut_name}}</span>
          <span>{{atcData.comm_count}}条评论</span>
          <!-- <span>{{atcData.pubdate | getTime}}</span> -->
          <span>{{atcData.pubdate | days}}</span>
        </div>
      </div>
      <van-image
        v-if="atcData.cover.type === 1"
        class="imgs"
        fit="cover"
        :src="atcData.cover.images[0]"
      />
    </van-cell>
  </div>
</template>

