/*
* 文章相关请求模块
*/

import request from '@/utils/request'

// 请求频道列表 (需要传递一个频道id)  频道新闻推荐_V1.1接口
export const getList = params => request({
  url: '/app/v1_1/articles',
  method: 'GET',
  params
})

// 请求全部频道
export const allList = () => request({
  url: '/app/v1_0/channels',
  method: 'GET'
})

// 请求添加频道到用户的频道列表(接口有问题，后台不能修改频道列表)
// 批量修改用户频道列表（部分覆盖）
export const addUserCha = data => request({
  url: '/app/v1_0/user/channels',
  method: 'PATCH',
  data
})

// 请求删除用户的频道列表中的指定频道(传要删除频道的id)(接口有问题，后台不能修改频道列表)
// 删除指定用户频道
export const popUserCha = pindaoId => request({
  url: `/app/v1_0/user/channels/${pindaoId}`,
  method: 'DELETE'
})

// 请求文章详情
export const getArticleData = pindaoId => request({
  url: `/app/v1_0/articles/${pindaoId}`,
  method: 'GET'
})

// 请求文章收藏(需要文章id)
export const ArticleCollection = target => request({
  url: '/app/v1_0/article/collections',
  method: 'POST',
  data: {
    target
  }
})
// 请求文章取消收藏(需要文章id)
export const ArticleUncollection = target => request({
  url: `/app/v1_0/article/collections/${target}`,
  method: 'DELETE'
})

// 请求文章点赞需要文章id)
export const ArticlePraise = target => request({
  url: '/app/v1_0/article/likings',
  method: 'POST',
  data: {
    target
  }
})
// 请求文章取消点赞(需要文章id)
export const ArticleUnpraise = target => request({
  url: `/app/v1_0/article/likings/${target}`,
  method: 'DELETE'
})
