<template>
  <div class="chapters" ref='chapters'>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" :success-text="suc" success-duration="800" >
      <van-empty image="error" description="暂无数据" v-if="nullList" />
      <van-list
        v-else
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        error: false
        :error.sync="error"
        error-text="请求失败，点击重新加载"
        @load="onLoad"
      >
        <atcItem v-for="(item, index) in list" :key="index" :atcData="item"></atcItem>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getList } from '@/api/article'
import atcItem from '../../components/atcItem'
// 引入函数防抖功能（避免频繁）
import { debounce } from 'lodash'

export default {
  name: 'chapters',
  components: {
    atcItem
  },
  props: {
    chapter: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      error: false,
      nullList: false, // 用于判断是否显示缺省页
      timeDate: null, // 用于储存上一次请求的时间戳
      refreshing: false, // 下拉刷新的loding状态
      suc: '',
      onScroll: 0 // 记录列表滚动高度
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {
    // 监听滚动事件 获取到滚动高度
    const chaDom = this.$refs.chapters
    // 使用debounce包裹函数，避免获取滚动高度过于频繁
    chaDom.onscroll = debounce(() => {
      // console.log(chaDom.scrollTop)
      // 记录滚动高度
      this.onScroll = chaDom.scrollTop
    }, 50)
  },
  methods: {
    async onLoad () {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1)
      //   }
      //   // 加载状态结束
      //   this.loading = false
      //   // 数据全部加载完成
      //   if (this.list.length >= 40) {
      //     this.finished = true
      //   }
      // }, 1000)
      try {
        // 请求数据 (传递请求参数进去)
        const { data } = await getList({
          channel_id: this.chapter.id, // 频道的id
          timestamp: this.timeDate || new Date().getTime(), // timeDate为空就表示第一次请求，直接用当前时间戳请求最新，
          with_top: 1
        })
        // for (const i of data.data.results) {
        //   this.list.push(i)
        // }
        if (data.data.results.length) {
          this.list.push(...data.data.results) // 这里用了数组的解构...数组 （等同于上面的变量）
          this.loading = false // 加载完一次就停止加载（等待滚动条触底再加载）
          this.timeData = data.data.pre_timestamp
        } else {
          if (this.list.length) {
            this.finished = true // 没有数据了就停止加载，表示加载完了
          } else {
            this.nullList = !this.nullList
          }
        }
      } catch (err) {
        this.loading = false // 异常就停止加载
        this.error = true // 异常就点击重新加载
      }
    },
    // 下拉刷新
    async onRefresh () {
      try {
        // 请求数据 (传递请求参数进去)
        const { data } = await getList({
          channel_id: this.chapter.id, // 频道的id
          timestamp: new Date().getTime(), // 传当前时间戳，获取最新数据
          with_top: 1
        })
        this.list.unshift(...data.data.results) // 向前添加进文章列表
        this.refreshing = false
        this.suc = `加载了${data.data.results.length}条数据`
      } catch (err) {
        console.log(err)
      }
    }
  },
  // 组件获取焦点/被激活时执行该函数
  activated () {
    // 把记录的滚动高度设置给列表
    this.$refs.chapters.scrollTop = this.onScroll
  }
}
</script>

<style>
  /*给这个文章列表组件一个固定定位，让他在一定范围内溢出滚动加载，不影响其他组件*/
  .chapters{
    width: 100%;
    position: fixed;
    bottom: 50px;
    top: 87px;
    overflow: scroll
  }
  .van-pull-refresh{
    overflow: unset;
  }
</style>
