<template>
    <div class="pindaoSet">
      <!--我的频道-->
      <van-cell title="我的频道" :border="false">
        <button slot="extra" class="rightBtn" @click="setbtn">{{!btn ? '编辑' : '完成'}}</button>
      </van-cell>
      <div class="grid">
        <div
          v-for="(item, index) in plists"
          :key="index"
          :class="actives === index ? 'oItem' : 'gridItem'"
          @click='popbtn(index, item)'
        >
          <i class="icon icon-guanbi1" v-if="btn && index !== 0"></i>
          <span>{{item.name}}</span>
        </div>
      </div>
      <!--频道推荐-->
      <van-cell title="频道推荐" :border="false">
      </van-cell>
      <div class="grid">
        <div
          v-for="(item, index) in boonList"
          :key="index"
          class="gridItem"
          @click='onAdd(item)'
        >
          <span>{{boonList[index].name}}</span>
        </div>
      </div>
    </div>
</template>

<script>
import { allList, addUserCha, popUserCha } from '@/api/article.js'
import { setItem } from '@/utils/uUser.js'

export default {
  name: 'pindaoSet',
  data () {
    return {
      pAllList: [], // 存储全部频道
      btn: false // 用于控制编辑模式
    }
  },
  created () {
    this.theAllList()
  },
  methods: {
    // 获取全部频道
    async theAllList () {
      try {
        const { data } = await allList()
        this.pAllList = data.data.channels
      } catch (err) {
        console.log(err)
      }
    },
    // 控制编辑状态
    setbtn () {
      this.btn = !this.btn
    },
    // 编辑状态
    async popbtn (index, item) {
      // 编辑状态(btn=true) (推荐频道不能删)
      if (this.btn && index !== 0) {
        // 删除频道
        this.plists.splice(index, 1)
        // 删除频道的数据持久化(通过token判断登录了没有)
        if (this.$store.state.token) {
          await popUserCha(item.id) // 调用封装的请求方法，请求删除指定的用户频道(传要删除频道的id)
          // 没有登录就本地存储
        } else {
          // 把删除后的列表重新覆盖存储到本地
          setItem(this.plists, 'user-channels')
        }
        // 删除的索引小于展示的索引，就要往前移动一位(因为删之前的 索引会-1，但是当前展示的索引又不会变！)
        if (index < this.actives) {
          this.$emit('act', this.actives - 1)
        }
        // 非编辑状态
      } else {
        // 切换频道
        this.$emit('clone', index) // 关闭频道列表,把点击的该频道下标发送给父组件
      }
    },
    async onAdd (item) {
      this.plists.push(item)
      // 数据持久化（登录和非登录状态）
      if (this.$store.state.token) {
        // 封装的添加列表的请求模块
        // 需要传递一个列表，列表中是对象，对象中id是添加频道的id；seq是要添加进的序号(因为上面已经push把这一个频道添加进去了，所以直接获取我的频道列表长度就添加的频道所在下标)
        await addUserCha({
          channels: [
            { id: item.id, seq: this.plists.length }
          ]
        })
      } else {
        setItem(this.plists, 'user-channels') // 调用本地存储工具存储到本地
      }
    }
  },
  computed: {
    // 计算推荐频道列表
    boonList () {
      // 判断我的频道列表是否已有该频道
      return this.pAllList.filter(index => {
        // 找到就返回值-为true 没找到返回undefult-为false （前面加个!非，就表示没找到的就添加进列表，找到的表示有就舍弃）
        return !this.plists.find(item => {
          // 返回已有的列表和全部的列表中都有的频道
          return item.id === index.id
        })
      })
    }
  },
  props: {
    // 我的频道列表
    plists: {
      type: Array,
      required: true
    },
    // 当前激活频道
    actives: {
      type: Number,
      required: true
    }
  }
}
</script>

<style scoped>
  .pindaoSet{
    top: 54px;
    height: 613px;
    position: fixed;
    overflow: scroll;
  }
  .van-cell{
    font-size: 16px;
  }
  .rightBtn{
    padding: 0 20px;
    border-radius: 50px;
    background-color: unset;
    border: 1px solid #ff5757;
    color: #ff5757;
    font-size: 14px;
  }
  .grid{
    margin: 0 16px;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
  }
  .grid>div{
    height: 38px;
    width: 80px;
    border-radius: 5px;
    margin-bottom: 5.73px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
  }
  .oItem{
    color: #f84b3b;
    background-color: rgba(250, 36, 25, 0.1)
  }
  .gridItem{
    color: #222;
    background-color: #f4f5f6;
  }
  .grid>div:not(:nth-child(4n+4)){
    margin-right: 5.73px;
  }
  .grid>div>span{
    font-size: 13px;
  }
  .grid>div>i{
    font-size: 14px;
    position: absolute;
    color: #444;
    top: 0;
    right: -2px;
  }
</style>
