<template>
    <div class="home-conent">
      <!-- 导航栏 -->
      <navbar>
        <router-link tar='div' to="/search" slot="title" class="soushuo" >
          <i class="icon icon-sousuo"></i>
          <span>搜索</span>
        </router-link>
      </navbar>
      <div class="tabs">
        <van-tabs v-model="active" sticky color="#1989fa" line-width='20px'>
          <van-tab v-for="index in tabCont" :key='index.id' :title="index.name">
            <!-- 文章列表 -->
            <chapters :chapter='index'></chapters>
          </van-tab>
        </van-tabs>
      </div>
      <!-- 频道列表 -->
      <div class='pList'>
        <i class="hbg icon icon-gengduo" @click="showPopup"></i>
      </div>
      <van-popup v-model="show"
        position="bottom"
        class='popList'
        :closeable='true'
        close-icon-position='top-left'
        get-container="body"
      >
        <pindaoSet :plists='tabCont'  @act='pBtn' :actives='active' @clone='cloneSet'></pindaoSet>
      </van-popup>
    </div>
</template>

<script>
import navbar from '../../components/navbar'
import { getChanner } from '@/api/user.js'
// 文章列表组件
import chapters from './chapters.vue'
// 引入频道编辑列表
import pindaoSet from './pindaoSet.vue'
// 引入封装的获取本地数据的工具方法
import { getItem } from '@/utils/uUser.js'

export default {
  name: 'HomeIndex',
  data () {
    return {
      active: 0, // 当前展示频道
      tabCont: [],
      show: false // 控制分类列表
    }
  },
  components: {
    navbar,
    chapters,
    pindaoSet
  },
  methods: {
    // 用于请求用户频道列表
    async getChanners () {
      // 判断是否登录
      if (this.$store.state.token) {
        try {
          const { data } = await getChanner()
          this.tabCont = data.data.channels
        } catch (err) {
          console.log(err)
        }
      } else {
        // 没有登录，判断本地是否存有频道列表数据
        const locals = getItem('user-channels')
        if (locals) {
          this.tabCont = locals // 本地有就使用本地的
          // 本地没有就发送请求获取一次数据（默认推荐有频道列表）
        } else {
          try {
            const { data } = await getChanner()
            this.tabCont = data.data.channels
          } catch (err) {
            console.log(err)
          }
        }
      }
    },
    showPopup () {
      this.show = true
    },
    // 频道列表的切换频道
    cloneSet (index) {
      this.show = false // 关闭频道列表弹出层
      this.active = index // 指定当前展示的下标
    },
    pBtn (index) {
      this.active = index
    }
  },
  created () {
    // vue创建完成时就请求用户频道列表
    this.getChanners()
  },
  fllters: {}
}
</script>

<style scoped>
  .theme-colors {
      background-color: #1989fa;
  }
  .soushuo{
    width: 278px;
    height: 32px;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .soushuo>i{
    font-size: 16px;
    color: #fff;
    margin-right: 2px;
    box-sizing: border-box;
  }
  .soushuo>span{
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px
  }
  .van-tab__text{
    font-size: 14px;
  }
  .van-tabs__nav--complete{
    padding: 0 !important;
  }
  /deep/ .van-tabs--line .van-tabs__wrap{
    height: 100%
  }
  /deep/ .van-tab{
    height: 41px;
    padding: 0 20px;
    border-bottom: 1px solid #e6e6e6;
    border-right: 1px solid #e6e6e6;
  }
  /deep/ .van-sticky{
    height: 41px;
    padding-right: 40px
  }
  .pList{
    height: 40px;
    width: 40px;
    background-color: #fff;
    position: fixed;
    top: 46px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #e6e6e6;
  }
  .pList>.hbg{
    z-index: 10;
    color: #222;
    font-size: 18px
  }
  .popList{
    height: 100%;
  }
</style>
