// 用户相关的请求模块

import request from '@/utils/request'
// 因为是在非组件中使用，所以需要单独引入
// import store from '@/store/index.js'

// 登陆注册 (通过对象的形式传递登陆所需的2个参数进来)
export const login = data => request({
  url: '/app/v1_0/authorizations',
  method: 'POST',
  data
})

// 短信验证  接收到手机号参数直接拼接进来就好了
export const sendCode = mobile => request({
  url: `/app/v1_0/sms/codes/${mobile}`,
  method: 'GET'
})

// 请求用户信息
export const userData = () => request({
  url: '/app/v1_0/user',
  method: 'GET'
  // headers: {
  //   Authorization: `Bearer ${store.state.token.token}`
  // }
})

// 请求用户频道列表
export const getChanner = () => request({
  url: '/app/v1_0/user/channels',
  method: 'GET'
})

// 关注用户
export const addUser = target => request({
  url: '/app/v1_0/user/followings',
  method: 'POST',
  data: {
    target
  }
})

// 取消关注用户
export const delUser = target => request({
  url: `/app/v1_0/user/followings/${target}`,
  method: 'DELETE'
})

// 获取用户个人资料
export const userProfile = () => request({
  url: '/app/v1_0/user/profile',
  method: 'GET'
})

// 编辑用户个人资料
export const setUserInfo = data => request({
  url: '/app/v1_0/user/profile',
  method: 'PATCH',
  data
})

// 编辑用户照片资料（头像、身份证照片）
export const setPhoto = data => request({
  url: '/app/v1_0/user/photo',
  method: 'PATCH',
  data
})
